<template>
  <!-- 四格导航 -->
    <div class="four-nav">
      <span v-for="nav in navList">
      <router-link v-bind:to="nav[1]">{{nav[0]}}</router-link></li>
    </span>
    </div>
  </template>

  <script>
  export default {
    name: 'fourNav',
    data () {
      return {
          navList:[
          ['关于我','/aboutMe'],
          ['学习分享','/LearningSharing'],
          ['心情随笔','/informalEssay'],
          ['留言','/leavingMessage']
        ],
     }
   }
  }
  </script>

  <style scoped>
  
  /* 四格导航 */
  .four-nav{
    box-shadow: 2px 2px 10px #eee;
    padding: 18px;
    text-align: center;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 25px;
  }
  .four-nav span{
    line-height: 50px;
    display: inline-block;
    width: 40%;
    font-size: 16px;
    height: 50px;
  
    margin-bottom: 10px;
    border-radius: 10px;
  }
  a{
      color: #fff;
      text-decoration: none;
  }
  .four-nav span:nth-child(1){
    background-color: #666666;
  }
  .four-nav span:nth-child(2){
    background-color: #00cccc;
  }
  .four-nav span:nth-child(3){
    background-color: #cc9966;
  }
  .four-nav span:nth-child(4){
    background-color: #6633cc;
  }
  </style>



