<template>
  <!-- 个人信息 -->
  <div class="personal-information">
    <div class="information-top">
      <div class="head-portrait">
        <img v-bind:src="information.imgSrc" height="100" width="100" alt="tou">
      </div>
      <div class="brief-introduction">
        <span>博 主 :</span>
        <span>{{information.blogGer}}</span>
        <div class="brief-introduction-text"> 
          {{information.briefIntroductionIext}}
        </div>
      </div>
    </div>
    <div class="information-middle">
      <a class="qq"><i class='iconfont'>&#xe612;</i></a>
      <a  class='weixin'><i class='iconfont'>&#xe709;</i></a>
      <a class='tel'><i class='iconfont'>&#xe618;</i></a>
      <a :href="information.weibo" class="sina"><i class='iconfont'>&#xe655;</i></a>         
    </div>
    <div class="information-bottom">
      <span>浏览次数:{{information.times}}</span>
      <span>运行天数:{{information.days}}</span>
    </div>
  </div>
</template>

<script>
// import Bus from '../../static/js/bus.js'
export default {
  name: 'personalInformation',
  data () {
    return {
      information:{
        imgSrc:'../../static/head.png',
        blogGer:'LKJNNG',
        briefIntroductionIext:'这是一初学者搭建的Blog,也许看起来很简陋，很不成熟。但是我相信在不久的将来它一定是成熟的，完美的！',
        times:0,
        days:0,
        weibo:'https://weibo.com/u/3901941793/home?wvr=5&lf=reg'
      }
    }
  }
}
</script>

<style scoped>
/* 字体图标 */
@font-face {
  font-family: 'iconfont';  /* project id 574559 */
  src: url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.eot');
  src: url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.woff') format('woff'),
  url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.svg#iconfont') format('svg');
}
.iconfont{
  font-family:"iconfont";
  font-size:24px;
  font-style:normal;
  color: #eee;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale; 
  /* padding-left:20px */
}
/* 个人信息 */
.personal-information{
  box-shadow: 2px 2px 10px #eee;
  margin-bottom: 25px;
  padding: 18px;
  color: #666;
}
.information-top{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.brief-introduction{
  margin-left: 20px;
}
.brief-introduction span:nth-child(1){
  padding: 5px 14px;
  background-color: #4196d9;
  color: #fff;
}
.brief-introduction-text{
  margin-top: 8px;
  font-size: 15px;
}
a{
  text-decoration: none;
}
i{
  cursor: pointer;
}
.qq i:hover{
    /* padding: 8px;
    background-color: #999;
    border-radius: 50%; */
    color: #0090ce;
  }
  .weixin i:hover{
    color: green;
  }
  .tel i:hover{
    color: #333;
  }
  .sina i:hover{
   color: red;
 }
 .information-bottom{
  margin-top: 10px;
}
  </style>



