import blogIndex from '../components/blogIndex.vue'
import showBlog from '../components/showBlog.vue'
import aboutMe from '../components/aboutMe/aboutMe.vue'
import learningSharing from '../components/learningSharing/learningSharing.vue'
import informalEssay from '../components/informalEssay/informalEssay.vue'
import leavingMessage from '../components/leavingMessage/leavingMessage.vue'
import write from '../components/write/write.vue'
import showNews from '../components/showNews.vue'

export default[
	{path:"/",component:blogIndex},
	{path:"/aboutMe",component:aboutMe},
	{path:"/learningSharing",component:learningSharing},
	{path:"/informalEssay",component:informalEssay},
	{path:"/leavingMessage",component:leavingMessage},
	{path:"/write",component:write},
	{path:"/blog/:id",component:showBlog},
	{path:"/eassy/:id",component:showBlog},
	{path:"/news/:id",component:showNews}
]


