<template>
  <div id="show-blog">
    <hr>
    <div class="container">
      <div class="container2">
       <showBlogContent v-bind:id='id'></showBlogContent>
        <div class="right">
          <blogGerInformation></blogGerInformation>
          <postRecommendation></postRecommendation>
        </div>
      </div>    
    </div>
  </div>
</template>

<script>
import blogGerInformation from './blogGerInformation.vue'
  import showBlogContent from './showBlogContent.vue'
  import postRecommendation from './postRecommendation.vue'
  export default{
    name:"show-blog",
    data(){
      return{
        id:this.$route.params.id,
        blog:{}
      }
    },
     components: {
    showBlogContent,postRecommendation,blogGerInformation
  }
  }
</script>
<style scoped>
#show-blog{
  font-family: '微软雅黑';
  width: 1225px;
  margin: 0 auto;
  background-color: #fff;
}
hr{
  background-color : #4196d9;
  height: 2px;
  border: none;
  top: -2px;
  position: relative;
  width: 94.28%;
  margin: 0 auto;
}
.container{
  width: 100%;
  max-width: 1155px;
  margin: 0 auto;
  padding: 0;
}
.container2{
  top: -20px;
  position: relative;
  overflow: hidden;
  padding-right: 380px
}
.right{
  padding: 50px 0;
  float: left;
  position: relative;
  right: -420px;
  width: 340px;
}
</style>


