<template>
  <div class="about-me">
  	<div class="img">
     <img :src="imgsrc"> 
    </div>
    <div class="memorial_day">
      <div class="time-axis"></div>
      <ul>
        <li v-for='(axis,index) in timeAxis' :class="'n'+index">
          <span>{{axis[0]}}</span>
          <div :class="'data-view'+index">{{axis[1]}}</div>
        </li>
      </ul>
    </div>  
  	<div class="personal">
     <h2>Just about me</h2> 
      <p class="personal-text">
        {{personalText}}
      </p>
      <p>个人微信号:<span>{{weixin}}</span></p>
      <p>微信请备注:博客</p>
      <div class="icon">
         <a class="qq"><i class='iconfont'>&#xe612;</i></a>
         <a class='weixin'><i class='iconfont'>&#xe709;</i></a>
         <a class='tel'><i class='iconfont'>&#xe618;</i></a>
         <a class="sina"><i class='iconfont'>&#xe655;</i></a>
       </div>

    </div>
  </div>
</template>

<script>
import mainBlog from '../mainBlog.vue'
import indexRight from '../indexRight.vue'
export default {
  name: 'aboutMe',
  data () {
    return {
      imgsrc:'../../../static/pics01.jpg',
      timeAxis:[
        ['大二结束','2017-07'],
        ['求职','2017-09'],
        ['进公司实习','2017-10'],
        ['工作的第一个项目','2018-01'],
        ['创建个人博客','2018-02'],
      ],
      personalText:'合理分配自我介绍的时间前文说过，自我介绍一般也就持续1—3分钟，所以应聘者得合理分配时间。常规安排是：第一段用于表述个人基本情况，中段重点谈自己的工作经历或社会实践经验，最后展望下自己的职位理想。但如果自我介绍被要求在1分钟完成，应聘者就要有所侧重，突出最有料的一点。在实践中，有些应聘者试图在短短的时间内吐露自己的全部经历，而有些应聘者则是三言两语就完成了自我介绍，这些都是不明智的做法。突出和应聘职位相关的信息自我介绍的内容不宜太多的停留在诸如姓名、教育经历等部分上，因为面试官可以在应聘者的简历上一目了然地看到这些内容。应聘者应该在自我介绍时选择一至两项跟自己所应聘的职位相关的经历和成绩作简述，以证明自己确实有能力胜任所应聘的工作职位。一个让人更有机会在面试中出彩的方法是在做一段自我介绍后适当停顿。比如在“我曾在大学期间组织过有2000人参与的大型校园活动”之后的停顿可能会引导面试官去问“那是什么样的活动呢？”，这样做的目的是为面试的深入打下基础。一切以事实说话在证明自己确实有能力胜任所应聘的工作职位时，应聘者可以使用一些小技巧，如介绍自己做过的项目或参与过的活动来验证某种能力，也可以适当地引用老师、同学、同事等第三方的言论来支持自己的描述。而这一切的前提是以事实为基础，因为自吹自擂一般是很难逃过面试官的眼睛的，一旦被发现掺假，基本预示着应聘者将被无情“秒杀”。以道谢结尾一个有用的建议：在自我介绍完后不要忘了道声“谢谢”，同时也把提问权还给面试官。这些小细节往往会影响面试官对应聘者的印象。',
      weixin:'lkjnng'
    }
  }
}
</script>

<style scoped>
@font-face {
  font-family: 'iconfont';  /* project id 574559 */
  src: url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.eot');
  src: url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.woff') format('woff'),
  url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.svg#iconfont') format('svg');
}
.iconfont{
  font-family:"iconfont";
  font-size:24px;
  /* color:#479cdf;  */
  font-style:normal;
  color: #eee;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale; 
  padding: 10px;
  border-radius: 50%;
  background-color: #999;
}
.about-me{
	font-family: '微软雅黑';
	width: 1225px;
	margin: 0 auto;
  padding-bottom: 1px;
	background-color: #fff;
  /* background-color: #4f86b6; */
}
.img{
  width: 1150px;
  height: 300px;
  margin:0 auto;
}
img{
  height: 300px;
  width: 1150px;
}
.memorial_day{
  margin: 10px auto;
  padding: 60px 80px;
}
.time-axis{
  height: 7px;
  background-color: #e9f5fb; 
  animation: timeAxis 5s;
  transition-timing-function: ease-out;
}
/* 时间轴动画 */
@keyframes timeAxis{
  0%{
    width: 0px;
  }
  4%{
    width: 4%
  }
  25%{
    width: 25%;
  }
  50%{
    width: 50%;
  }
  75%{
    width: 75%;
  }
  97%{
    width: 97%;
  }
  100%{
    width: 100%
  }
}
ul{
  list-style: none;
  position: relative;
}
ul li:before{
  content: '';
  position: absolute;
  border-radius: 50%;
  height: 16px;
  width: 16px;
  background-color: #479cdf;
  border: solid #e9f6fb 5px;
}
ul li span{
   position: absolute;
    color: #479cdf;
    top: 15px;
}
ul li div{
  position: absolute;
   color: #479cdf;
   top: -40px;
}
.n0 span,.data-view0{ 
  left: 30px;  
}
.n1 span,.data-view1{
  left: 240px;
}
.n2 span,.data-view2{
  left: 490px;
}
.n3 span,.data-view3{
  left: 740px;
}
.n4 span,.data-view4{
  left: 960px;
}
/* 动画 */
.n0:before{ 
  left: 40px;
  top: -15px;
}
.n1:before{ 
  left: 250px;
  top: -15px;
}
.n2:before{ 
  left: 500px;
  top: -15px;
}
.n3:before{ 
  left: 750px;
  top: -15px;
}
.n4:before{ 
  left: 970px;
  top: -15px;
}
.personal{
  padding: 0 80px 80px 80px;
}
h2{
  width: 200px;
  border-radius: 0 30px 30px 0;
  background-color: #479cdf;
  padding: 10px 5px;
  text-align: center;
  border-left: 10px solid #e9f5fb;
  color: #fff;
  box-shadow: #21566f 1px 1px 1px;
  /* box-shadow: 2px 2px 10px #eee; */
  margin-bottom: 30px
}
.personal-text{
  text-indent: 2rem;
  font-size: 15px;
  color: #666;
  line-height: 30px;
}
p{
  margin-bottom: 20px;
  color: #666;
}
.qq i:hover{
    /* padding: 8px;
    background-color: #999;
    border-radius: 50%; */
    color: #0090ce;
    background-color: #eee;
  }
  .weixin i:hover{
    color: green;
    background-color: #eee;
  }
  .tel i:hover{
    color: #333;
    background-color: #eee;
  }
  .sina i:hover{
   color: red;
   background-color: #eee;
 }
 a{
  cursor: pointer;
 }

</style>



