<template>
  	<div class="classified-sharing">
      <div class="classified">
        <span v-for='classified in classifications' v-on:click='arrangement(classified)'>{{classified}}</span>
        <span></span>
      </div>
  		<div class="blog" 
      v-for='(blog,index) in showBlogs' >
        <router-link v-bind:to="'/blog/'+ blog.id+'blog'">
          <h2>{{blog.title}}</h2>
        </router-link>
  			<div class="blog-content">
  				<ul>
  					<li><a>{{blog.author}}</a></li>
  					<li><a>发布时间 : <span>{{blog.data}}</span></a></li>
  					<li><a>分类 :&nbsp; {{blog.classification}}</a></li>
  				</ul>
  				<div class="blog-text">
  					{{blog.content | snippet}}
  				</div>	
          <router-link v-bind:to="'/blog/'+ blog.id+'blog'">
  				<button>All content</button>
        </router-link>
  			</div>
  		</div>
  	</div>
</template>

<script>
import Bus from '../../../static/js/bus.js'
export default {
  name: 'classified-sharing',
  data () {
    return {
      classifications:['默认','vue.js','Node.js','net.js','jq.js'],
      blogs:[],
      showBlogs:[],
      search:''
    }
  },
  created(){
  	this.$http.get('https://wd7590248622njwxih.wilddogio.com/posts.json')
  	.then(function(data){
  		return data.json()
  	})
  	.then(function(data){
  		var blogsArray = [];
  		for(let key in data){
  			data[key].id = key;
  			blogsArray.push(data[key]);
  		}
  		this.blogs = blogsArray
      this.showBlogs = this.blogs.slice(0,5)
  	})
  },
    methods:{
    arrangement:function(classified){
      if(classified == '默认'){
          var showBlogs = this.classifications.toString();
          this.showBlogs = this.blogs.filter((blog)=>{ 
            return showBlogs.match(blog.classification)
        })
          this.showBlogs = this.showBlogs.slice(0,5);
      }else if(classified == 'vue.js') {
        this.showBlogs = this.blogs.filter((blog)=>{  
             // alert(JSON.stringify(blog)) ;
          return blog.classification.match('vue.js');
        })
        this.showBlogs = this.showBlogs.slice(0,5)
      }else if(classified == 'Node.js') {
        this.showBlogs = this.blogs.filter((blog)=>{   
        return blog.classification.match('Node.js')
        })
        this.showBlogs = this.showBlogs.slice(0,5)
      }else if(classified == 'net.js') {
        this.showBlogs = this.blogs.filter((blog)=>{   
        return blog.classification.match('net.js')
        })
        this.showBlogs = this.showBlogs.slice(0,5)
      }else if(classified == 'jq.js') {
        this.showBlogs = this.blogs.filter((blog)=>{   
        return blog.classification.match('jq.js')
        })
        this.showBlogs = this.showBlogs.slice(0,5)
      }
    }
  },
   mounted(){
    Bus.$on('search',(e)=>{
      this.search = e;
      this.showBlogs = this.blogs.filter((blog)=>{   
        return blog.title.match(this.search)
      })

    })
  },
  filters:{
  	snippet(data){
  		return data.slice(0,120)+'...'
  	}
  }


}
</script>

<style scoped>
.classified-sharing{
	padding: 50px 0 0 0;
  float:left;
  margin-right: -380px;
  width: 99.9%;
}
/* .classified{
  border-right: 2px solid #4196d9;
} */
.classified span{
  text-align: center;
  color: #fff;
  display: inline-block;
  width: 15%;
  padding: 10px 0;
  background-color: #4196d9;
  margin-bottom: 25px;
  margin-right: 30px;
  box-shadow: 2px 2px 10px #eee;
  cursor: pointer;
}
.classified span:nth-child(2){
  background-color: #00cccc;
}
.classified span:nth-child(3){
  background-color: #cc9966;
}
.classified span:last-child{
  float: right;
  border-right: 2px solid #4196d9;
  width: 2px;
  height: 20px;
  margin-right: 0;
  cursor: auto;
}
.blog{
	max-width: 780px;
	margin: 0px 0px 25px 1px;
	padding: 15px;
	/* background-color: #d8dfe7; */
	box-shadow: 2px 2px 10px #eee;
}
a{
  text-decoration: none;
}
h2{
	font-size: 20px;
	font-weight: 300;
  color: #4196d9;
}
.blog-content>ul{
	margin: 12px 0 8px 0;
	list-style: none; 
	color: #999;
  font-size: 12px;

} 
.blog-content ul li{
	display: inline-block;
	margin-right: 20px;
	font-size: 16px;
}
/* .blog-content>ul>li>ul{
	float: right;
} */
.blog-text{
	padding:0 0 20px 0;
  font-size: 15px;
  color: #666;
}
button{
	font-size: 12px;
	background-color: #4196d9;
	border: none;
	color: #fff;
	padding: 12px 30px;
  cursor: pointer;
}
</style>



