<template>
  <div class="blog-index">
  	<slide-show></slide-show>
  	<div class="container">
  		<div class="container2">
  			<main-blog></main-blog>
  			<index-right></index-right>
  		</div> 		
  	</div>
  	
  </div>
</template>

<script>
import slideShow from './slideShow.vue'
import mainBlog from './mainBlog.vue'
import indexRight from './indexRight.vue'
export default {
  name: 'blogIndex',
  data () {
    return {
      blogs:[]
    }
  },
  components: {
    slideShow,mainBlog,indexRight
  }
}
</script>

<style scoped>
.blog-index{
	font-family: '微软雅黑';
	width: 1225px;
	margin: 0 auto;
	background-color: #fff;
}
.container{
  width: 100%;
	max-width: 1155px;
	margin: 0 auto;
  padding: 0;
}
.container2{
	overflow: hidden;
	padding-right: 380px
}

</style>



