<template>
    <div class="main-message">
     <div class="container">
       <h2>
         <b>留言板</b>
         <span>我们相遇了，便不是过客，因为您愿意为我停留</span>
       </h2>
       <div class="head">
         <img src="../../../static/head1.jpg"alt="头像">
         <div class="head-right">
           <p>您驻足于此，便注定了我们的缘分。从此我都会在此默默守候，等待缘分再次相遇，我们已是朋友，前方的道路也许就能并肩前行了。所以！请珍惜彼此。</p>
         </div>
       </div>
       <div class="content">
         
       </div>
      </div>
    </div>
</template>

<script>
import Bus from '../../../static/js/bus.js'
export default {
  name: 'main-message',
  data () {
    return {
      blogs:[],
      search:''
    }
  }
}
</script>

<style scoped>
.main-message{
  padding: 50px 0 0 0;
  float:left;
  margin-right: -380px;
  width: 99.9%;
}
.container{
  padding: 12px;
  margin-left: 1px;
  box-shadow: 2px 2px 10px #eee;
}
h2{

}
h2 b{
  display: inline-block;
  width: 200px;
  letter-spacing: 10px;
  border-radius: 0 30px 30px 0;
  background-color: #479cdf;
  padding: 10px 5px;
  text-align: center;
  border-left: 10px solid #e9f5fb;
  color: #fff;
  box-shadow: #21566f 1px 1px 1px;
  /* box-shadow: 2px 2px 10px #eee; */
  margin-bottom: 30px
}
h2 span{
  float: right;
  font-size: 12px;
  color: #479cdf;
  padding: 10px 5px;
  line-height: 30px;
}
.head{
 display: flex;
 justify-content: space-around;
 align-items: center;
}
.head img{
  width: 170px;
  height: 170px;
  border-radius: 50%;
}
.head-right{
  position: relative;
}
.head-right p{
  padding: 15px 20px;
  width: 430px;
  background-color: #eee;
  border-radius: 10px;
  font-size: 14px;
  color: #333;
}
.head-right p:before{
    content: "";
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 20px 20px 20px 0;
    border-color: transparent #eee transparent;
    position: absolute;
    left: -20px;
    top: 24px;
}

</style>



