<template>
 <div class="news">
  <div class="news-con" >
    <h2>原标题：{{news.title}}</h2>
    <div class="blog-content">
      <ul>
       <li><a>发布时间 : <span>{{news.time}}</span></a></li>
       <li><a>来源 : <span>{{news.src}}</span></a></li>
       <li><a>分类 : <span>{{news.category}}</span></a></li>
     </ul>
     <div class="img" v-html='pic'>

     </div>
     <div class="blog-text" v-html='news.content'>
     </div>	
   </div>
 </div>
</div>
</template>

<script>
import Bus from '../../static/js/bus.js'
export default {
  name: 'news',
  props:['id'],
  data(){
    return {
      news:{},
      pic:'',
    }
  },
  methods:{
    updata_(){
     var num = +this.id.replace(/[^0-9]/ig,"");
     this.news = data.result.list[num];
     if (this.news.pic !== '') {
        this.pic = '<img src="'+this.news.pic +'" alt="图片">';
      }else{
        this.pic = '';
      }
    }
  },
created(){
  this.$http.jsonp('http://api.jisuapi.com/news/get?channel=头条&start=0&num=30&appkey=b4758c13721b6db4', { credentials: true })
  .then(function(data){
    return data.json()
  })
  .then(function(data){
    var blogsArray = [];
       // alert(this.id);
       console.log(data.result.list);  
       var num = +this.id.replace(/[^0-9]/ig,"");
       this.news = data.result.list[num];

       if (this.news.pic !== '') {
        this.pic = '<img src="'+this.news.pic +'" alt="图片">';
      }else{
        this.pic = '';
      }

      // this.postRecommendation.newstList = data.result.list.slice(0,10);
      // this.postRecommendation.hotList = data.result.list.slice(10,20);
      // this.postRecommendation.recommendList = data.result.list.slice(20,30);
    })   
},
mounted(){
  this.updata_()
}
}
</script>

<style scoped>
.news{
	padding: 50px 0 0 0;
  float:left;
  margin-right: -380px;
  width: 99.9%;
}
.news-con{
	max-width: 780px;
	margin: 0px 0px 25px 1px;
	padding: 15px;
	/* background-color: #d8dfe7; */
	box-shadow: 2px 2px 10px #eee;
}
a{
  text-decoration: none;
}
h2{
	font-size: 20px;
	font-weight: 300;
  color: #4196d9;
}
.blog-content>ul{
	margin: 12px 0 8px 0;
	list-style: none; 
	color: #999;
  font-size: 12px;

} 
.blog-content ul li{
	/* display: inline-block; */
	margin-right: 20px;
  padding: 5px 0;
  font-size: 14px;
}
/* .blog-content>ul>li>ul{
	float: right;
} */
.img{
  text-align: center;
}
.img img{
  margin-top: 20px;
  height: 100%
}
.blog-text{
  line-height: 2.5;
  padding:10px 0 20px 0;
  font-size: 15px;
  color: #666;
}

button{
 font-size: 12px;
 background-color: #4196d9;
 border: none;
 color: #fff;
 padding: 12px 30px;
 cursor: pointer;
}
</style>



