<template>
  	<div class="show-blog-content">
  		<div class="blog" >
  			<h2>{{blog.title}}</h2>
  			<div class="blog-content">
  				<ul>
  					<li><a>{{blog.author}}</a></li>
  					<li><a>发布时间 : <span>{{blog.data}}</span></a></li>
  					<li><a>分类 :&nbsp; {{blog.classifications}}</a></li>
  				</ul>
  				<div class="blog-text">
  					{{blog.content}}
  				</div>	
  			</div>
  		</div>
  	</div>
</template>

<script>
// import Bus from '../../static/js/bus.js'
export default {
  name: 'showBlogContent',
  props:['id'],
  data () {
    return {
      blog:{}
    }
  },
  created(){
    if (this.id.slice(-4,this.id.length) == 'blog') {
      var ids = this.id.slice(0,-4);
      var url = 'https://wd7590248622njwxih.wilddogio.com/posts/'+ids + '.json'
    }else{
      var ids = this.id.slice(0,-5);
      var url = 'https://wd7590248622njwxih.wilddogio.com/essay/'+ids + '.json'

    }
  	this.$http.get(url)
  	.then(function(data){
  		console.log(data);
  		return data.json()
  	})
  	.then(function(data){
  		this.blog = data
  	})
  },  
}
</script>

<style scoped>
.show-blog-content{
  padding: 50px 0 0 0;
  float:left;
  margin-right: -380px;
  width: 99.9%;
}
.blog{
  max-width: 780px;
  margin: 0px 0px 25px 1px;
  padding: 15px;
  /* background-color: #d8dfe7; */
  box-shadow: 2px 2px 10px #eee;
}
h2{
  font-size: 20px;
  font-weight: 300;
  color: #4196d9;
}
.blog-content>ul{
  margin: 12px 0 8px 0;
  list-style: none; 
  color: #999;
  font-size: 12px;

} 
.blog-content ul li{
  display: inline-block;
  margin-right: 20px;
  font-size: 14px;
}
/* .blog-content>ul>li>ul{
  float: right;
} */
.blog-text{
  /* text-indent: 2rem; */
  padding:0 0 20px 0;
  font-size: 15px;
  color: #666;
  line-height: 30px;
}
 
button{
  font-size: 12px;
  background-color: #4196d9;
  border: none;
  color: #fff;
  padding: 12px 30px;
}
</style>



