         Bus.$emit('login_box_show',isShow)
    },
    cancellation:function(){
      this.login_data =JSON.parse(sessionStorage.login_data)
    },
    logout_:function(){
      this.login_data.Identification = false;
      sessionStorage.login_data = JSON.stringify(this.login_data);
      Bus.$emit('OutLine',this.login_data);
      alert('注销成功！')
    }
  },
  mounted(){
