<template>
 <div class="write">
  <div class="container">
    <div class="write_container"  v-if='!isSubmited'>
      <div class="add_header">
        <div class="add_header_left " v-bind:class='{active:!isShow}' v-on:click='switch_(0)' >写博客</div>
        <div class="add_header_right" v-bind:class='{active:isShow}' v-on:click='switch_(1)'>写随笔</div>
      </div>
      <div class="write_blog" v-if='!isShow ' >
        <form action="">
          <div class="write_title display_"><label>主题 :</label>
            <input type="text" value="" v-model='Blog.title'></div>
            <div class="write_content display_" ><label>内容 :</label>
              <textarea name="" id="" cols="30" rows="10"  v-model='Blog.content'></textarea></div>
              <div class="classification display_" > 
                <label>分类 :</label><div class="checkboxs">
                 <lable>vue.js</lable>
                 <input type="radio" value="vue.js" v-model='Blog.classification'>
                 <lable>Node.js</lable>
                 <input type="radio" value="Node.js" v-model='Blog.classification'>
                 <lable>jq.js</lable>
                 <input type="radio" value="jq.js" v-model='Blog.classification'>
                 <lable>net.js</lable>
                 <input type="radio" value="net.js" v-model='Blog.classification'>
               </div></div>
               <div class="write_author display_">
                <label>作者 :</label> <select v-model='Blog.author' >
                  <option value="林康景"  >林康景</option>
                  <option value="LKJNNG" >LKJNNG</option>
                  <option value="年华" >年华</option>
                </select></div>
                <button v-on:click.prevent='post(0)'>添加</button>
              </form>
            </div>
            <div class="write_essay" v-if='isShow'>
              <form action="">
                <div class="write_title display_"><label>主题 :</label>
                  <input type="text" value="" v-model='Essay.title'></div>
                  <div class="write_content display_"><label>内容 :</label>
                    <textarea name="" id="" cols="30" rows="10"  v-model='Essay.content'></textarea></div>
                    <div class="classification display_" > 
                      <label>分类 :</label><div class="checkboxs">
                       <lable>诗歌</lable>
                       <input type="radio" value="诗歌" v-model='Essay.classification'>
                       <lable>感悟</lable>
                       <input type="radio" value="感悟" v-model='Essay.classification'>
                       <lable>日记</lable>
                       <input type="radio" value="日记" v-model='Essay.classification'>
                     </div></div>
                     <div class="write_author display_">
                      <label>作者 :</label> <select v-model='Essay.author'>
                        <option value="林康景">林康景</option>
                        <option value="LKJNNG">LKJNNG</option>
                        <option value="年华">年华</option>
                      </select></div>
                      <button v-on:click.prevent='post(1)'>添加</button>
                    </form>
                  </div>
                </div>
                <div class="success" v-if='isSubmited'>
                  <h1>发布成功</h1>
                  <div class="overview">
                    <h3>博客总览</h3>
                    <p>主题：{{showCon.title}}</p>
                    <p>内容：</p>
                    <p>{{showCon.content}}</p>
                    <p>分类：{{showCon.classification}}</p>
                    <p>作者：{{showCon.author}}</p>
                  </div>
                  <button v-on:click='return_'>返回</button>
                </div>

              </div>
            </div>
          </template>

          <script>
// import Bus from '../../static/js/bus.js'
export default {
  name: 'write',
  data () {
    return {
      Blog:{
        title:'',
        content:'',
        classification:'',
        author:'林康景'
      },
      Essay:{
        title:'',
        content:'',
        classification:'',
        author:'林康景'
      },
      showCon:'',
      isShow:false,
      isSubmited:false
    }
  },
  methods:{
    switch_:function(index){
      if(index == 0){
        this.isShow = false
      }else{
        this.isShow = true
      }
    },
    post:function(index){
      var url;
      var data_;
      if(index == 0){
        url = 'https://wd7590248622njwxih.wilddogio.com/posts.json'
        data_ = this.Blog;

      }else if(index == 1){
        url ='https://wd7590248622njwxih.wilddogio.com/essay.json'
        data_ = this.Essay;
      }
      this.showCon = data_
      this.$http.post(url,data_)
      .then(function(data){
        console.log(data);
        this.isSubmited = true;
      });
    },
    return_:function(){
      history.go(-1)
    }
  }
}
</script>

<style scoped>
.write{
  font-family: '微软雅黑';
  width: 1225px;
  margin: 0 auto;
  padding-bottom: 1px;
  background-color: #fff;
  overflow: hidden;
}
.write_container{
  height: 100%;
  width: calc(100% - 40px);
  margin: 0 auto;
  padding: 20px 0;
  color: #4196d9;
}
.add_header{
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.add_header div{
  width: 49.5%;
  flex-grow: 1;
  text-align: center;
  border: 1px solid #4196d9;
  padding: 10px 0;
  cursor: pointer;
}
.add_header div.active{
  color: #fff;
  background-color: #4196d9;
}
.write_blog, .write_essay{
  width: 100%;
  padding: 50px 20px;
}
form>div{
  margin-bottom: 50px;

}
form>div>*:nth-child(2){
  margin-left: 20px;

}
.display_{
 display: flex;
 justify-content: flex-start;
 align-items: flex-start;
}
label{
  color: #4196d9;
  font-size: 18px;
}
input[type='text']{
  height: 40px;
  width: 60%;
  text-indent:12px;
  border: 1px solid #4196d9; 
  color: #4196d9;
  font-size: 14px;
}
textarea{
  width: 80%;
  border: 1px solid #4196d9;
  color: #4196d9;
  padding: 10px 12px;
  line-height: 20px;
  font-size: 14px;
}
input[type='radio']{
  margin-right: 10px;
  font-size: 14px;
}
select{
  width: 20%;
  height: 40px;
  text-indent: 20px;
  border: 1px solid #4196d9;
  color: #4196d9;
  font-size: 14px;
}
option{
  padding: 10px 20px;
}
button{
  height: 50px;
  width: 120px;
  background-color: #4196d9;
  border: none;
  color: #fff;
  font-size: 18px;
  cursor: pointer;
}
h1{
  margin: 20px 20px;
  /* text-align: center; */
  color: #4196d9;
}
.overview {
 border: 1px solid #4196d9;
 color: #4196d9;
 font-size: 20px;
 width: 60%;
 border-radius: 10px;
 margin: 0  auto 0px 20px;
 padding: 10px 15px;
}
.overview h3, p{
  padding-bottom: 20px;
}
.success button{
  margin: 20px auto 50px 20px;
}
</style>



