<template>
  <div class="blogHeader">
  	<div class="lkjblog">
  		<h1>LKJBLOG</h1>
    	<h3>BY LKJNNG</h3>
    </div>
    <div class="blogHeaderContent">
    	<div class="nav">
    		<ul>
	    		<li 
	    		v-for="(nav,index) in blogHeader.navList"
	    		:class="{active:index === blogHeader.nowIndex}"
	    		><router-link v-bind:to="nav[1]" exact>{{nav[0]}}</router-link></li>
	    	</ul>
	    <div class="navLeft"></div>
	    <div class="navRight"></div>
	    </div>
	    
    </div>  
  </div>
</template>

<script>
import Bus from '../../static/js/bus.js'
export default {
  name: 'blogHeader',
  data () {
    return {
      blogHeader:{
      	navList:[
      		['首页','/'],
      		['关于我','/aboutMe'],
      		['学习分享','/LearningSharing'],
      		['心情随笔','/informalEssay'],
      		['留言','/leavingMessage']
      	]
      },
      login_data:''
    }
  },
  mounted(){
	this.onLine(),
	Bus.$on('OutLine',(e)=>{
		sessionStorage.login_data =JSON.stringify(e);		
		this.login_data =JSON.parse(sessionStorage.login_data);
		// this.login_data =sessionStorage.login_data;
  		if (this.login_data.Identification == false) {
			this.blogHeader.navList.pop();
		}
	}),
	Bus.$on('OnLine',(e)=>{
		sessionStorage.login_data =JSON.stringify(e);
		this.login_data =JSON.parse(sessionStorage.login_data);
		// this.login_data =sessionStorage.login_data;
  		if (this.login_data.Identification == true) {
			this.blogHeader.navList.push(['撰写','write'])
		}
	})
	
  },
  methods:{
  	onLine(){
  		this.login_data =JSON.parse(sessionStorage.login_data)
  		if (this.login_data.Identification == true) {
			this.blogHeader.navList.push(['撰写','write'])
		}
  	}
  }
}
</script>

<style scoped>
.blogHeader{
	background-color: #479cdf;
	width: 100%;
	height: 375px;
	overflow: hidden;
	position: relative;
}
.lkjblog{
	height: 30%;
	width: 20%;
	margin: 140px auto;
	color: #fff;
	text-align: center;
}
.lkjblog h1{
	font-size: 55px;
	color: inherit;
}
h3{
	font-size:24px;
	color: #a5d0f2;
}
.blogHeaderContent{
	height: 90px;
	width: 1225px;
	margin:auto;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: #fff;
}
.nav{
	position: relative;
	width: 1265px;
	height: 50px;
	margin:10px auto;
	background-color: #f5f7f6;
	text-align: center;
	left: -20px
}
ul{
	list-style: none;
}
li{
	
	display: inline-block;	
	font-size: 17.5px;	
}
li a{
	position: relative;	
	top: 5px;
	color: #666;
	padding: 10px 30px;
	text-decoration: none;
}
.active{
	background-color:  #479cdf;
	
}
.active a{
	/* color: #fff!important;
	background-color:  #479cdf; */
}
.router-link-active{
	color: #fff!important;
	background-color:  #479cdf;
}
.navLeft{
	position: absolute;
	left: 0;
	bottom: -20px;
	width: 0px;
	height: 0px;	
	border-left: 10px solid #479cdf;
	border-bottom:  10px solid #479cdf;
	border-top: 10px solid #9fb2b5;
	border-right: 10px solid #9fb2b5;
}
.navRight{
	position: absolute;
	right: 0;
	bottom: -20px;
	width: 0px;
	height: 0px;	
	border-right: 10px solid #479cdf;
	border-bottom:  10px solid #479cdf;
	border-top: 10px solid #9fb2b5;
	border-left: 10px solid #9fb2b5;
	
}
</style>



