<template>
	<!-- 帖子推荐 -->
	<div class="post-recommendation">
		<div class="post-recommendation-title">
			<span 
			v-on:mouseenter="switchPost(index)"
			v-bind:class="{active:index == postRecommendation.nowIndex}"
			v-for='(tab,index) in postRecommendation.tabsParam'
			>{{tab}}</span>
		</div>
		<div class="post-recommendation-content" >
			<div class="newest active" v-show="postRecommendation.nowIndex === 0">
				<ul>					
					<li v-for= '(newest,index) in postRecommendation.newstList'><span>{{index+1}}</span>
						<router-link v-bind:to="'/news/'+ index+'new'">{{newest.title}}</router-link> 
					</li> 					                   
				</ul>
			</div>
			<div class="hot" v-show="postRecommendation.nowIndex === 1">
				<ul>
					<li v-for= '(hot,index) in postRecommendation.hotList'><span>{{index+1}}</span>
						<router-link v-bind:to="'/news/'+1+ index+'new'">{{hot.title}}</router-link>
					</li>                      
				</ul>
			</div>
			<div class="recommend" v-show="postRecommendation.nowIndex === 2">
				<ul>
					<li v-for= '(recommend,index) in postRecommendation.recommendList'><span>{{index+1}}</span>
						<router-link v-bind:to="'/news/'+2+ index+'new'">{{recommend.title}}</router-link>
					</li>                      
				</ul></div>
			</div>       
		</div>

	</template>

	<script>
	export default {
		name: 'index-right',
		data () {
			return {
	        postRecommendation:{ //帖子推荐
	           tabsParam:['最新','热门','推荐'],//
	           nowIndex:0,
	           newstList:[],//最新新闻
	           hotList:[],//热门新闻
	           recommendList:[],//推荐新闻
	           // isActive :true
	       }
   }
},
methods:{
	switchPost:function(index){
		this.postRecommendation.nowIndex = index;
	}
},
created(){
	this.$http.jsonp('http://api.jisuapi.com/news/get?channel=头条&start=0&num=30&appkey=b4758c13721b6db4', { credentials: true })
	.then(function(data){
		return data.json()
	})
	.then(function(data){
		var blogsArray = [];
		console.log(data.result.list);   
		this.postRecommendation.newstList = data.result.list.slice(0,10);
		this.postRecommendation.hotList = data.result.list.slice(10,20);
		this.postRecommendation.recommendList = data.result.list.slice(20,30);
	})
},


}
</script>

<style scoped>
/* 字体图标 */
@font-face {
	font-family: 'iconfont';  /* project id 574559 */
	src: url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.eot');
	src: url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.eot?#iefix') format('embedded-opentype'),
	url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.woff') format('woff'),
	url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.ttf') format('truetype'),
	url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.svg#iconfont') format('svg');
}
.iconfont{
	font-family:"iconfont";
	font-size:24px;
	font-style:normal;
	color: #eee;
	-webkit-font-smoothing: antialiased;
	-webkit-text-stroke-width: 0.2px;
	-moz-osx-font-smoothing: grayscale; 
	/* padding-left:20px */
}
/* 帖子推荐 */
.post-recommendation{
	box-shadow: 2px 2px 10px #eee;
	margin-bottom: 25px;
	color: #666;
}
.post-recommendation-title{
	display: flex;
	justify-content: space-between;
	align-items: center;  
}
.post-recommendation-title>span{
	flex-grow: 1;
	text-align: center;
	padding: 12px 24px;
	background-color: #eee;
	border-top: #eee 2px solid;
	cursor: pointer;
}
.post-recommendation-title>.active{
	background-color: #fff!important;
	color: #666;
	border-top: #4196d9 2px solid;
}
.post-recommendation-content{

}
/* .post-recommendation-content>.active{
  display: block!important;
  } */
  .post-recommendation-content div ul{
  	list-style: none;
  }
  .post-recommendation-content div ul li{
  	padding: 12px 0px 0px 12px;
  	text-decoration: none;
  	display: flex;
  	justify-content: flex-start;
  	align-items: center;
  }
  .post-recommendation-content div ul li:last-child{
  	padding-bottom: 12px;
  }
  .post-recommendation-content div ul li span{
  	flex-grow: 0;
  	flex-shrink: 0;
  	flex-basis: 18px;
  	text-align: center;
  	line-height: 18px;
  	background-color: #c00;
  	font-size: 12px;
  	color: #fff;
  }
  .post-recommendation-content div ul li:nth-child(n+4) span{
  	background-color: #4196d9;
  }
  .post-recommendation-content div ul li a{
  	text-decoration: none;
  	margin:0 12px;
  	font-size: 15px;
  	color: #333;
  	overflow: hidden;
  	white-space: nowrap;
  	text-overflow: ellipsis;
  }
  </style>



