<template>  
  <div class="slide-show" v-on:mouseover="stop()" v-on:mouseout="move()">  
    <div class="slideshow">  
      <transition-group tag="ul" name="image" class='transition-group'>  
        <li v-for="(img, index) in imgArray" v-show="index===mark" :key="index">  
          <a href="#">  
            <img :src='img'>  
          </a>  
        </li>  
      </transition-group>  
    </div>  
    <div class="bar">  
      <span v-for="(item, index) in imgArray" :class="{ active:index===mark }"  
      @click="change(index)" :key="index"></span>  
    </div>  
  </div>  
</template>  
  
<script>  
export default {  
  name: 'slide-show',
  data () {  
    return {  
      timer: null, //定时器  
      mark: 0, //比对图片索引的变量  
      imgArray: [  
        '../../../static/pics01.jpg',  
        '../../../static/pics02.jpg',  
        '../../../static/pics03.jpg',  
        '../../../static/pics04.jpg'  
      ]  
    }  
  },  
  methods: {  
    autoPlay () {  
      this.mark++;  
      if (this.mark === 4) {  
        this.mark = 0  
      }  
    },  
    play () {  
      this.timer = setInterval(this.autoPlay, 3500)  
    },  
    change (i) {  
      this.mark = i  
    },  
    stop () {  
      clearInterval(this.timer)  
    },  
    move () {  
      this.timer = setInterval(this.autoPlay, 3500)  
    }  
  },  
  created () {  
    this.play()  
  }  
}  
</script>

<style scoped>  
  * {  
    margin: 0;  
    padding: 0;  
    list-style: none;  
  }  
  .slide-show {  
    width: 1155px;  
    height: 350px;  
    margin: 0 auto;  
    /* margin-top: 50px;   */
    overflow: hidden;  
    position: relative;  
    box-shadow: 2px 2px 10px #eee;
  }  
  .slideshow {  
    width: 1155px;  
    height: 350px;  
  }  
  .transition-group{
    list-style: none;
  }
  li {  
    position: absolute;  
  }  
  img {  
    width: 1155px;  
    height: 350px;  
  }  
  /* 轮播图切换效果 */
  .image-enter-active {  
    transform: translateX(0);  
    transition: all 1.5s ease;  
  }  
  .image-leave-active {  
    transform: translateX(-100%);  
    transition: all 1.5s ease;  
  }  
  .image-enter {  
    transform: translateX(100%);  
  }  
  .image-leave {  
    transform: translateX(0);  
  }  
  /* 轮播图切换效果end */
  .bar {  
    position: absolute;  
    width: 100%;  
    bottom: 10px;  
    margin: 0 auto;  
    z-index: 10;  
    text-align: center;  
  }  
  .bar span {  
    width: 10px;  
    height: 10px;  
    background: white;  
    display: inline-block;  
    margin-right: 10px;  
  }  
  .active {  
    background: #479cdf !important;  
  }  
</style>  


