}
.container{
  width: 100%;
  max-width: 1155px;
  margin: 0 auto;
  padding: 0;
}
.container2{
  top: -20px;
  position: relative;
  overflow: hidden;
  padding-right: 380px
}
.right{
  padding: 50px 0;
  margin-right: 2px;
  float: left;
  position: relative;
  right: -420px;
  width: 340px;
}
</style>


