<template>
  <!-- 搜索 -->
  <div class="search-container">
    <div class="search"  >
      <input type="search" placeholder="搜索博客" v-model='search.searchText' v-on:change='bus'>  
      <i class="iconfont">&#xe6d1;</i>
    </div>        
  </div>
</template>

  <script>
  import Bus from '../../static/js/bus.js'
  export default {
    name: 'searchContainer',
    data () {
      return {
        search:{
          searchText:''
        },
     }
   },
   methods:{
      bus:function(){
        var msg = this.search.searchText
      // console.log(msg);
        Bus.$emit('search',msg)
      }
    }
  }
  </script>

  <style scoped>
  /* 字体图标 */
  @font-face {
    font-family: 'iconfont';  /* project id 574559 */
    src: url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.eot');
    src: url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.eot?#iefix') format('embedded-opentype'),
    url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.woff') format('woff'),
    url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.ttf') format('truetype'),
    url('//at.alicdn.com/t/font_574559_6op2xjeywwjnhfr.svg#iconfont') format('svg');
  }
  .iconfont{
    font-family:"iconfont";
    font-size:24px;
    font-style:normal;
    color: #eee;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale; 
    /* padding-left:20px */
  }
  /* 搜索 */ 
  .search-container{
    box-shadow: 2px 2px 10px #eee;
    overflow: hidden;
    width: 100%;
    margin-bottom: 25px;
  }
  .search{
    width: 70%;
    border-radius: 5px;
    border: 1px solid #eee;
    height: 40px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 25px auto;
  }
  input[type = search]{
    width: 88%;
    height: 100%;
    border: none;
    outline: none;
    text-indent: 1rem;
    color: #666;
  }
  .search .iconfont{
    color: #eee!important;
    font-size:1.2rem;
    margin-right: .5rem;
  }
  </style>



