<template>
  	<div class="essay-blog">
  		<div class="blog" v-for='(blog,index) in filteredBlogs' v-show='index < 5'>
  			<router-link v-bind:to="'/blog/'+ blog.id+'essay'">
          <h2>{{blog.title}}</h2>
        </router-link>
  			<div class="blog-content">
  				<ul>
  					<li><a>{{blog.author}}</a></li>
  					<li><a>发布时间 : <span>{{blog.data}}</span></a></li>
  					<li><a>分类 :&nbsp; {{blog.classifications}}</a></li>
  				</ul>
  				<div class="blog-text">
  					{{blog.content | snippet}}
  				</div>	
          <router-link v-bind:to="'/blog/'+ blog.id+'essay'">
  				<button>All content</button>
        </router-link>
  			</div>
  		</div>
  	</div>
</template>

<script>
import Bus from '../../../static/js/bus.js'
export default {
  name: 'essay-blog',
  data () {
    return {
      blogs:[],
      search:''
    }
  },
  computed:{
    filteredBlogs:function(){
      return this.blogs.filter((blog)=>{   
        return blog.title.match(this.search)
      })
    }
  },
  created(){
  	this.$http.get('https://wd7590248622njwxih.wilddogio.com/essay.json')
  	.then(function(data){
  		console.log(data);
  		return data.json()
  	})
  	.then(function(data){
  		var blogsArray = [];
  		for(let key in data){
  			data[key].id = key;
  			blogsArray.push(data[key]);
  		}
  		this.blogs = blogsArray
  	})
  },
   mounted(){
    Bus.$on('search',(e)=>{
      this.search = e
    })
  },
  filters:{
  	snippet(data){
  		return data.slice(0,120)+'...'
  	}
  }


}
</script>

<style scoped>
.essay-blog{
	padding: 50px 0 0 0;
  float:left;
  margin-right: -380px;
  width: 99.9%;
}
.blog{
	max-width: 780px;
	margin: 0px 0px 25px 1px;
	padding: 15px;
	/* background-color: #d8dfe7; */
	box-shadow: 2px 2px 10px #eee;
}
a{
  text-decoration: none;
}
h2{
	font-size: 20px;
	font-weight: 300;
  color: #4196d9;
}
.blog-content>ul{
	margin: 12px 0 8px 0;
	list-style: none; 
	color: #999;
  font-size: 12px;

} 
.blog-content ul li{
	display: inline-block;
	margin-right: 20px;
	font-size: 16px;
}
.blog-text{
	padding:0 0 20px 0;
  font-size: 15px;
  color: #666;
}
 
button{
	font-size: 12px;
	background-color: #4196d9;
	border: none;
	color: #fff;
	padding: 12px 30px;
  cursor: pointer;
}
</style>



