<template>
	<div class="login-box" v-on:click.self='close_' v-if='isShow'>
		<div class="container">
			<div class="login_content">
				<h2>博主登录</h2>
				<div class="username_box">  
					<i class='iconfont'>&#xe66c;</i>    	
					<input type="text" id="username" placeholder="用户名" v-model='login_data.username'>
				</div>
				
				<div class="password_box">
					<i class='iconfont'>&#xe62f;</i>
					<input type="password" id="password" placeholder="密码" v-model='login_data.password'>
				</div>
				
				<div class="login_box">
					<input type="button" id="login_" value="登录" v-on:click='submit_'>
				</div>
				<div class="sign_out_box">
					<input type="button" id="sign_out" value="退出" v-on:click='close_'>
				</div>
				<div class="prompt" v-if='prompt'>
					{{prompt}}
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Bus from '../../static/js/bus.js'
export default {
	name: 'login-box',
	data () {
		return {
			isShow:false,
			login_data:{
				username:'',
				password:'',
				Identification:''
			},
			prompt:''
		}
	},
	methods:{
		close_:function(){
			this.isShow = false;
		},
		submit_:function(){
			if(this.login_data.username == '' || this.login_data.username.length <6 ){
				this.prompt='用户名最少为6位';
			}else if(this.login_data.password == '' || this.login_data.password.length <6 ){
				this.prompt='密码最少为6位';
			}else if(this.login_data.username == 'lkjnng' && this.login_data.password ==123456){
				this.login_data.Identification = true;
				this.isShow = false;
				sessionStorage.login_data = JSON.stringify(this.login_data);
				Bus.$emit('OnLine',this.login_data);
				alert('登录成功！')
			}else{
				this.prompt='账号或密码错误，请重新输入';
			}
		},
	},
	mounted(){
		Bus.$on('login_box_show',(e)=>{
			this.isShow = e
		})
	},
}
</script>

<style scoped>
@font-face {
	font-family: 'iconfont';  /* project id 594567 */
	src: url('//at.alicdn.com/t/font_594567_zs3s4skbuc5l8fr.eot');
	src: url('//at.alicdn.com/t/font_594567_zs3s4skbuc5l8fr.eot?#iefix') format('embedded-opentype'),
	url('//at.alicdn.com/t/font_594567_zs3s4skbuc5l8fr.woff') format('woff'),
	url('//at.alicdn.com/t/font_594567_zs3s4skbuc5l8fr.ttf') format('truetype'),
	url('//at.alicdn.com/t/font_594567_zs3s4skbuc5l8fr.svg#iconfont') format('svg');
}
.iconfont{
	font-family:"iconfont";
	font-size:24px;
	color:#479cdf; 
	font-style:normal;
	/* color: #eee; */
	-webkit-font-smoothing: antialiased;
	-webkit-text-stroke-width: 0.2px;
	-moz-osx-font-smoothing: grayscale; 
	padding: 10px;
}
.login-box{
	height: 100%;
	width: 100%;
	background-color: rgba(0,0,0,0.5);
	position: fixed; 	
	left: 0;
	top: 0;
	bottom: 0;
	z-index: 998;
}
.container{
	position: absolute;
	width:360px;
	height: 300px; 
	background-color: #fff;
	left: calc(50% - 180px);
	top: calc(50% - 150px);
	z-index: 999;
}
.login_content{
	width: 80%;
	margin: 0 auto;
}
.container div{
	height: 40px;
	margin-bottom: 25px;
}
h2{
	padding: 20px 0;
	color: #479cdf;
	font-weight: 300;	
	font-size: 24px;
}
.username_box{
	border: 1px solid #479cdf;	
	display: flex;
	justify-content: flex-start;
	align-items: center;
}
#username{
	border: none;
	width: calc(100% - 50px);
	height: 100%;
	/* text-indent: 14px; */
	outline: none;

}
.password_box{
	border: 1px solid #479cdf;
	display: flex;
	justify-content: flex-start;
	align-items: center;
}
#password{
	border: none;
	width: calc(100% - 50px);
	height: 100%;
	outline: none;
}
.prompt{
	color: red;
	width: 100%;
	margin-top: -25px;
	padding: 10px 0;
	font-size: 14px;
	white-space:nowrap;
}
.login_box, .sign_out_box{
	width: 45%;
	display: inline-block;
}
.sign_out_box{
	float: right;
}
#login_, #sign_out{
	text-align: center;
	border: none;
	width: 100%;
	height: 100%;
	background-color: #479cdf; 
	color: #fff;
	font-size: 18px;
	cursor: pointer;
}
</style>



